import React, { useState,Fragment} from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { Dialog, Transition } from '@headlessui/react'
import { XMarkIcon } from '@heroicons/react/24/outline'
import { Link, Navigate } from 'react-router-dom';
import { deleteItemAsync, selectItems, updateQuantityAsync } from './cartSlice';
export default function Cart() {
  const [open, setOpen] = useState(true)
  const products=useSelector(selectItems);
  const dispatch = useDispatch();

  const handleQuantity=(e,product)=>{
    dispatch(updateQuantityAsync({id:product.id,quantity:+e.target.value}))
  }

  const handleRemove=(e,id)=>{
    dispatch(deleteItemAsync(id));
  }

  const totalAmount=products.reduce((amount,item)=>item.product.price*item.quantity +amount,0);
  const totalItems=products.reduce((total,item)=>item.quantity +total,0);

  return (<>
    {!products.length && <Navigate to='/' replace={true}></Navigate>}
  
    <div className="mx-auto bg-white mt-20 max-w-7xl px-4 sm:px-6 lg:px-8">
                    <div className="border-t border-gray-200 px-4 py-6 sm:px-6">
                         <h2 className="text-4xl font-bold tracking-tight text-gray-900 my-5">SHOPPING CART</h2>
                    </div>

                    <div className="border-t border-gray-200 px-4 py-6 sm:px-6">
                        <div className="flow-root">
                          <ul role="list" className="-my-6 divide-y divide-gray-200">
                            {products.map((product) => (
                              <li key={product.id} className="flex py-6">
                                <div className="h-24 w-24 flex-shrink-0 overflow-hidden rounded-md border border-gray-200">
                                  <img
                                    src={product.product.thumbnail}
                                    alt={product.product.title}
                                    className="h-full w-full object-cover object-center"
                                  />
                                </div>

                                <div className="ml-4 flex flex-1 flex-col">
                                  <div>
                                    <div className="flex justify-between text-base font-medium text-gray-900">
                                      <h3>
                                        <a href={product.product.href}>{product.product.title}</a>
                                      </h3>
                                      <p className="ml-4">{product.product.price}/-</p>
                                    </div>
                                    <p className="mt-1 text-sm text-gray-500">{product.product.color}</p>
                                  </div>
                                  <div className="flex flex-1 items-end justify-between text-sm">
                                    <div className="text-gray-500">
                                      <label htmlFor="quantity" className="inline mr-5 text-sm font-medium leading-6 text-gray-900">
                                        {product.quantity}
                                      </label>
                                       <select onChange={(e)=>handleQuantity(e,product)}>
                                        <option value="1">1</option>
                                        <option value="2">2</option>
                                        <option value="3">3</option>
                                        <option value="4">4</option>
                                        <option value="5">5</option>
                                       </select>
                                    </div>

                                    <div className="flex">
                                      <button
                                      onClick={(e)=>handleRemove(e,product.id)}
                                        type="button"
                                        className="font-medium text-indigo-600 hover:text-indigo-500"
                                      >
                                        Remove
                                      </button>
                                    </div>
                                  </div>
                                </div>
                              </li>
                            ))}
                          </ul>
                        </div>
                    </div>
                   
                    <div className="border-t border-gray-200 px-4 py-6 sm:px-6">
                      <div className="flex justify-between text-base font-medium text-gray-900">
                        <p>Subtotal</p>
                        <p>{totalAmount} /-</p>
                      </div>
                      <div className="flex justify-between my-2 text-base font-medium text-gray-900">
                        <p>Total Items In Cart</p>
                        <p>{totalItems} items</p>
                      </div>
                      <p className="mt-0.5 text-sm text-gray-500">Shipping and taxes calculated at checkout.</p>
                      <div className="mt-6">
                        <Link to='/checkout' className="flex items-center justify-center rounded-md border border-transparent bg-indigo-600 px-6 py-3 text-base font-medium text-white shadow-sm hover:bg-indigo-700">
                          Checkout
                        </Link>
                      </div>
                      <div className="mt-6 flex justify-center text-center text-sm text-gray-500">
                        <p>
                          or
                          <Link to="/">
                          <button
                            type="button"
                            className="font-medium text-indigo-600 hover:text-indigo-500"
                            onClick={() => setOpen(false)}
                          >
                            Continue Shopping
                            <span aria-hidden="true"> &rarr;</span>
                          </button>
                          </Link>
                        </p>
                      </div>
                    </div>
    </div>
    </>
  );
}
